



























import Vue from 'vue';

export default Vue.extend({
  name: 'PlayerInfo',
  computed: {
    playerName(): string {
      if (this.$store.state.currentSessionId) {
        return this.$store.state.users[this.$store.state.currentSessionId].name;
      }
      return '先选择一位玩家';
    },
  },
});
